<template>
  <div>
    <Modal v-model="modal" title="查看物料模板" :styles="{width:'100%'}" class-name="modal-box">
      <div class="master-content">
        <div class="row">
          <label>物料模板名称：</label>
          <span>{{templateName}}</span>
          <!-- <Input v-model="templateName" type="text" name="templateName" placeholder="请输入姓名" style="width: 300px">{{materialName}}</Input> -->
        </div>
        <div class="row">
          <label>代码：</label>
          <Input v-model="code" type="textarea" :rows="8" disabled placeholder="请输入模板代码"
                 style="width:300px;height:200px;"></Input>
        </div>
        <div class="row">
          <label>说明：</label>
          <Input v-model="desc" type="textarea" :rows="8" disabled placeholder="暂无说明"
                 style="width:300px;height:200px;"></Input>
          <!-- <textarea disabled v-model="desc" placeholder="(选填)" style="width:300px;height:100px;">

                </textarea> -->
        </div>
      </div>
      <div slot="footer">
        <Button class="btnColor" type="text" @click="modal=false">返回</Button>
        <!--  <Button type="primary" @click="" >确定</Button> -->
      </div>
    </Modal>
  </div>

</template>

<script>
  export default {
    data() {

      return {
        modal: false,
        name: '',
        soltNameLen: '最多可输入100个字符',
        isAtive: false,
        materialTemplateId: '',
        valueTextarea: '',
        materialName: '',
        templateName: '',
        templateCodeArea: '',
        materialTemplateName: '',
        materialTemplateCode: '',
        templateDesc: '',
        code: '',
        desc: ''
      }
    },
    created() {

    },
    methods: {
      beforeLoad() {
        this.templateName = "";
        this.code = ""
        this.desc = "";
        this.modal = true;
      },
      getMaterialTemplateById(materialTemplateId) {
        let url = `/materialTemplate/getMaterialTemplateById.json?materialTemplateId=${materialTemplateId}`;
        this.$Http.get(url, {}).then(res => {
          if (res.body.status == 0) {
            console.log(res.body.data);
            let arr = res.body.data;

            this.templateName = arr.materialTemplateName;
            this.desc = arr.materialTemplateDesc;
            this.code = arr.materialTemplateCode
          }
        });
      },

    }
  }
</script>

<style scoped>
  .label-right-active {
    color: red;
  }

  .label-left {
    display: inline-block;
    width: 70px;

  }

  .full {
    margin-bottom: 20px;
  }

  .label-right-active-full {
    display: inline-block;
    width: 300px;
  }

  .ivu-input-wrapper, .label-left {
    display: inline-block;
    vertical-align: top;
  }

  .label-left {
    display: inline-block;
    width: 100px;
  }

  .btn-wraps {
    position: fixed;
    bottom: 50px;
  }
</style>



