<template>
  <div>
    <Modal v-model="modal"
           title="修改物料模板"
           :styles="{width:'100%'}"
           class-name="modal-box">
      <div class="master-content">
        <div class="row">
          <label>物料模板名称：</label>
          <Input v-model="templateName" type="text" name="templateName" placeholder="请输入模板名称" @on-blur='userDataNum'
                 @on-focus='onFocusName' style="width: 300px">{{materialName}}</Input>
          <span :class="{'label-right-active':isAtive}" class="label-right">{{soltNameLen}}</span>
        </div>
        <div class="row">
          <label>代码：</label>
          <Input v-model="templateCodeArea" type="textarea" :rows="8" placeholder="请输入模板代码"
                 style="width:300px;height:200px;" @on-blur='blurVal'></Input>
          <span style="width:300px; display:inline-block;" class="label-right"
                :class="{'label-right-active':isAtiveCf}">{{descString}}</span>
        </div>
        <div class="row">
          <label>说明：</label>
          <Input v-model="templateDesc" type="textarea" :rows="8" placeholder="(选填)"
                 style="width:300px;height:100px;"></Input>
        </div>
      </div>
      <div slot="footer">
        <Button type="text" @click="cancel">取消</Button>
        <Button class="btnColor" type="primary" @click="updateMaterialTemplate">修改</Button>
      </div>
    </Modal>
  </div>

</template>

<script>
  export default {
    data() {

      return {
        modal: false,
        name: '',
        soltNameLen: '最多可输入100个字符',
        descString: '常用语法规则：(1)声明参数：var 参数名:数据类型=0或1; //参数含义说明数据类型包括media(图片或flash)、link(点击链接)、text(文本)、number(数值)0或1表示是否必填，0为选填，1为必填例如，var imglink:link=1;//图片点击链接(2)引用参数：${参数名};例如：<a href="${imglink}" target="_blank">...</a>',
        isAtive: false,
        descString2: '常用语法规则：(1)声明参数：var 参数名:数据类型=0或1; //参数含义说明数据类型包括media(图片或flash)、link(点击链接)、text(文本)、number(数值)0或1表示是否必填，0为选填，1为必填例如，var imglink:link=1;//图片点击链接(2)引用参数：${参数名};例如：<a href="${imglink}" target="_blank">...</a>',
        isAtiveCf: false,
        materialTemplateId: '',
        valueTextarea: '',
        materialName: '',
        templateName: '',
        templateCodeArea: '',
        materialTemplateName: '',
        materialTemplateCode: '',
        templateDesc: '',
        code: '',
        desc: ''
      }
    },
    created() {

    },
    methods: {
      beforeLoad() {
        this.descString = this.descString2;
        this.isAtiveCf = false;
        this.materialName = "";
        this.code = ""
        this.desc = "";
        this.modal = true;
      },
      cancel() {
        this.descString = this.descString2;
        this.isAtiveCf = false;
        this.soltNameLen = "最多可输入100个字符"
        this.isAtive = false;
        this.modal = false;
      },
      getMaterialTemplateById(materialTemplateId) {

        let url = `/materialTemplate/getMaterialTemplateById.json?materialTemplateId=${materialTemplateId}`;
        this.$Http.get(url, {}).then(res => {
          if (res.body.status == 0) {
            console.log(res.body.data);
            let arr = res.body.data;
            this.materialTemplateId = arr.fxAdMaterialTemplateId;
            this.templateName = arr.materialTemplateName;
            this.templateDesc = arr.materialTemplateDesc;
            this.templateCodeArea = arr.materialTemplateCode;

            // this.$emit("filterParent");
          }
        });
      },

      updateMaterialTemplate() {

        let templateCodeArea = encodeURIComponent(this.templateCodeArea);
        const url = `/materialTemplate/updateMaterialTemplate.json?`
        this.$Http.post(url, {
          materialTemplateCode: templateCodeArea,
          materialTemplateId: this.materialTemplateId,
          materialTemplateName: this.templateName,
          materialTemplateDesc: this.templateDesc
        }).then(res => {
          console.log(res);
          if (res.body.status == 0) {
            // this.$router.push("/orderManagement");
            this.modal = false;
            this.$emit("filterParent");
          } else {
            this.modal = true;
          }
        })
      },

      onFocusName() {
        this.soltNameLen = "最多可输入100个字符";
        this.isAtive = false;

      },
      userDataNum() {
        //姓名位数不在0-100间
        if (this.templateName) {
          var reg = /^[\s\S]{1,100}$/;
          var r = this.templateName.match(reg);
          if (r == null) {
            this.soltNameLen = "长度超过一百个字符"
            this.isAtive = true;
          }
        } else {
          this.soltNameLen = "物料名称不能为空"
          this.isAtive = true;
        }
      },
      blurVal() {
        let regStart = /<fx168start>/;
        let regEnd = /<fx168end>/;
        var rS = this.templateCodeArea.match(regStart);
        var rE = this.templateCodeArea.match(regEnd);
        console.log(rS + "====" + rE);
        if (this.templateCodeArea) {
          if (rS == "<fx168start>" && rE == "<fx168end>") {
            this.descString = this.descString2;
            this.isAtiveCf = false;
          } else {
            this.descString = "您输入的模板代码中不包含<fx168start>、<fx168end>，请重新输入"
            this.isAtiveCf = true;
          }
        } else {
          this.descString = "模板代码不能为空"
          this.isAtiveCf = true;
        }

      }
    }
  }
</script>

<style scoped>
  .label-right-active {
    color: red;
  }

  .label-left {
    display: inline-block;
    width: 70px;

  }

  .full {
    margin-bottom: 20px;
  }

  .label-right-active-full {
    display: inline-block;
    width: 300px;
  }

  .ivu-input-wrapper, .label-left {
    display: inline-block;
    vertical-align: top;
  }

  .label-left {
    display: inline-block;
    width: 100px;
  }

  .btn-wraps {
    position: fixed;
    bottom: 50px;
  }
</style>



