<template>
  <div>
    <top-title title="物料模板管理"></top-title>
    <div class="fu">
      <div class="topBtnWrap">
        <div>
          <Button class="btnColor" icon="plus" type="primary" @click="newMaterialTemlate">新建物料模板</Button>
          <Button class="btnCancel" type="error" :disabled="isDeletes" @click="deleteAll">删除</Button>


          <Button type="text" v-on:click="trigger">筛选&nbsp;&nbsp;<Icon type="arrow-down-b"></Icon>
          </Button>


        </div>
        <div>
          <Input v-model="value" placeholder="输入关键字" style="width: 300px"></Input>
          <Button class="btnColor" type="primary" icon="search" @click='search'>搜索</Button>
        </div>
      </div>


      <div class="master-content-middle" v-show="isShow">
        <div class="label">
          状态：
        </div>
        <div class="label-value" id="filterForm">
          <Select v-model="status" style="width:200px" @on-change="selectStatus">
            <Option value="-99">全部</Option>
            <Option value="1">启用</Option>
            <Option value="0">删除</Option>
          </Select>
        </div>

      </div>


      <div style="margin-top:10px">
        <Table :loading="loading" border ref="selection" @on-select-all="" :columns="columns" :data="data1"
               @on-selection-change="selectData"></Table>
      </div>
      <Page @on-change='clickPage' :current="pageNo" :total="totalCount" show-sizer show-total
            @on-page-size-change="changePageSize"></Page>
      <Modal
        v-model="modal1"
        title="删除用户？"
        @on-ok="ok"
        @on-cancel="cancel">
        <p>确定要删除此条内容吗？</p>

      </Modal>
    </div>


    <materialTemlate ref="materialTemlate" v-on:filterParent="getFilterData"></materialTemlate>
    <changeMaterial ref="changeMaterial" v-on:filterParent="getFilterData"></changeMaterial>
    <showMaterial ref="showMaterial" v-on:filterParent="getFilterData"></showMaterial>
  </div>
</template>

<script>
  import topTitle from '@/components/common/topTitle'
  import materialTemlate from '@/components/pages/accountAndSettings/materialTemplate/child/setNewMaterialTemplate'
  import changeMaterial from '@/components/pages/accountAndSettings/materialTemplate/child/changeMaterialTemplate'
  import showMaterial from '@/components/pages/accountAndSettings/materialTemplate/child/showMaterialTemplate'

  export default {
    components: {topTitle, materialTemlate, changeMaterial, showMaterial},
    data() {
      return {
        isDeletes: true,
        data1: [],
        value: '',
        loading: true,
        fxAdMaterialTemplateId: '',
        // disabled:'true',
        status: '-99',
        routeParams: '',
        pageSize: 10,
        pageNo: 1,
        modal1: false,
        isShow: false,
        bolOk: null,
        paramsIndex: '',
        fxAdMaterialTemplateId: '',
        totalCount: 0,
        isAdds: true,
        isDeletes: true,
        addMaterialTemplateParams: [],
        deleteMaterialTemplateParams: [],
        columns: [
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title: '物料模板名称',
            key: 'materialTemplateName'
          },
          {
            title: '状态',
            key: 'status'
          },
          {
            title: '说明',
            key: 'explain'
          },
          {
            title: '物料数',
            key: 'materialNum'
          },
          {
            title: '操作',
            align: 'center',
            key: 'operate',
            width: 200,

            render: (h, params) => {
              let status = params.row.materialTemplateStatus;
              // alert(materialTemplateStatus)
              if (status === 0) {
                return h('div', [
                  h('Button', {
                    props: {
                      type: 'primary',
                      size: 'small'
                    },
                    style: {
                      marginRight: '5px',
                      backgroundColor: '#0676c4',
                      borderColor: '#0676c4'
                    },
                    on: {
                      click: () => {
                        this.$refs.showMaterial.beforeLoad();
                        this.$refs.showMaterial.getMaterialTemplateById(params.row.fxAdMaterialTemplateId);
                      }
                    }
                  }, '查看')

                ])
              }
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px',
                    backgroundColor: '#0676c4',
                    borderColor: '#0676c4'
                  },
                  on: {
                    click: () => {
                      this.$refs.showMaterial.beforeLoad();
                      this.$refs.showMaterial.getMaterialTemplateById(params.row.fxAdMaterialTemplateId);
                    }
                  }
                }, '查看'),
                h('Button', {
                  props: {
                    type: 'info',
                    size: 'small',

                  },
                  style: {
                    marginRight: '5px',
                    backgroundColor: '#1fa2fd',
                    borderColor: '#1fa2fd'
                  },
                  on: {
                    click: () => {
                      //this.$router.push({name:'changeMaterialTemplate'})


                      this.$refs.changeMaterial.beforeLoad();
                      this.$refs.changeMaterial.getMaterialTemplateById(params.row.fxAdMaterialTemplateId);
                    }
                  }
                }, '修改'),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small',
                  },
                  style: {

                    backgroundColor: '#ea0b00',
                    borderColor: '#ea0b00'
                  },
                  on: {
                    click: () => {
                      let v = [];
                      v.push(params.row);
                      this.deleteBox(v);
                    }
                  }
                }, '删除')
              ]);
            }

          }

        ],

      }
    },
    created() {
      this.initList();
    },
    methods: {
      selectData(selection) {
        console.log(selection)
        let canDeletes = selection.filter((item) => {
          return item.materialTemplateStatus == 1;
        });

        let canAdds = selection.filter((item) => {
          return item.materialTemplateStatus == 0;
        });
        console.log(canAdds)
        this.isAdds = !canAdds.length > 0;
        this.addMaterialTemplateParams = canAdds;
        this.isDeletes = !canDeletes.length > 0;
        this.deleteMaterialTemplateParams = canDeletes;
      },

      trigger: function () {

        this.isShow = !this.isShow;
      },

      deleteBox(params) {
        console.log(params);
        let materialTemplateNames = params.map((item, index) => {
          return item.materialTemplateName;
        });
        let materialTemplateIds = params.map((item, index) => {
          return item.fxAdMaterialTemplateId;
        });
        this.$Modal.confirm({
          title: '删除物料模版',
          content: `你确定要删除` + materialTemplateNames.join(","),
          onOk: () => {
            this.deleteMaterialData(materialTemplateIds.join(","));
            console.log(materialTemplateIds.join(","))
          }
        })
      },

      deleteMaterialData(materialTemplateId) {
        const url = "/materialTemplate/deleteMaterialTemplate.json?materialTemplateId=" + materialTemplateId;
        console.log(materialTemplateId)
        this.$Http.get(url, {}).then(res => {
          console.log(res)
          this.$Message.info(res.body.data);
          this.initList();
        })
      },

      deleteAll() {
        this.deleteBox(this.deleteMaterialTemplateParams);

      },
      selectStatus(status) {
        this.status = status;
        this.initList();
      },


      initList() {
        const userName = sessionStorage.getItem('userName');
        const url = "/materialTemplate/getMaterialTemplateList.json?nameLike=" + encodeURIComponent(this.value)
          + "&status=" + this.status + "&pageSize=" + this.pageSize + "&pageNo=" + this.pageNo
        // console.log(url)
        this.$Http.get(url, {}).then(res => {
          var arr = res.body.data.result;
          this.loading = false;
          this.totalCount = res.body.data.totalCount;
          console.log(res);
          let tablList = [];
          this.data1 = [];
          for (var key in arr) {
            // console.log(arr[key].roles)
            let rolesIdArr = arr[key].roles;
            //console.log(rolesIdArr);
            // if (arr[key].materialTemplateStatus == 0) {
            // 		arr[key].materialTemplateStatus = "删除"
            //       this.disabled = 'false';
            // }else if (arr[key].materialTemplateStatus == 1){
            // 		arr[key].materialTemplateStatus = "启用"
            // }
            tablList = {
              'materialTemplateName': arr[key].materialTemplateName,
              'materialTemplateStatus': arr[key].materialTemplateStatus,
              'status': arr[key].materialTemplateStatus == 0 ? '删除' : '启用',
              'explain': arr[key].materialTemplateDesc,
              'materialNum': arr[key].materialCount,
              'fxAdMaterialTemplateId': arr[key].fxAdMaterialTemplateId
            }
            // this.data1 = [];
            this.data1.push(tablList);


          }
        })
      },
      clickPage(data) {
        console.log(data);
        this.loading = true;
        this.pageNo = data;
        // this.data1 = [];
        this.initList();
      },
      changePageSize(pageSize) {
        console.log(pageSize);
        this.loading = true;
        this.pageNo = 1;
        this.pageSize = pageSize;
        // this.data1 = [];
        this.initList();
      },
      newMaterialTemlate: function () {
        this.$refs.materialTemlate.beforeLoad();
      },
      search() {
        this.pageNo = 1;
        this.initList()
      },
      // handleCancel () {
      //     this.value = "";
      //     this.initList ()
      // },
      ok() {
        const url = "/materialTemplate/deleteMaterialTemplate.json?materialTemplateId=" + this.fxAdMaterialTemplateId
        console.log(url);
        this.$Http.get(url, {}).then(res => {
          console.log(res);
          if (res.body.status == 0) {
            this.$Message.info(res.body.msg);
            this.data1.splice(this.paramsIndex, 1);
          } else if (res.body.status == 200) {
            this.$router.push({name: 'login'});
            this.$Message.info(res.body.msg);
          } else {
            this.$Message.info(res.body.msg);
          }


        })
      },
      cancel() {
        this.$Message.info('已取消删除');
      },
      selectAll(data) {
        console.log(data);
        var fxAdMaterialTemplateIdStr = "";
        for (var i = 0; i < data.length; i++) {
          if (i == data.length - 1) {
            fxAdMaterialTemplateIdStr += data[i].fxAdMaterialTemplateId
          } else {

            fxAdMaterialTemplateIdStr += data[i].fxAdMaterialTemplateId + ","
          }
        }
        this.fxAdMaterialTemplateId = fxAdMaterialTemplateIdStr;
        console.log(fxAdMaterialTemplateIdStr);

      },
      delateAll() {
        const url = "/materialTemplate/deleteMaterialTemplate.json?materialTemplateId=" + this.fxAdMaterialTemplateId
        this.$Http.get(url, {}).then(res => {
          console.log(res);
        })
      },
      getFilterData: function () {
        // this.pageSize = 10;
        // this.pageNo = 1;
        this.initList();
      },


    }

  }

</script>

<style scoped>
  .ivu-page {
    text-align: right;
    margin-right: 30px;
    padding: 10px 10px;
  }

  .topBtnWrap {
    display: flex;
    justify-content: space-between;
  }

  .master-content-middle {
    display: -webkit-flex; /* Safari */
    display: flex;
    padding: 30px 10px;
    border: 1px solid #d7f0f2;
    align-items: center;
    margin-top: 10px;
  }

  .master-content-middle .label {
    padding: 0px 20px;
  }
</style>



